<template>
  <el-card>
    <div>
      <el-form ref="userForm" :model="userForm" label-width="100px" style="width: 100%; margin: 20px auto">
        <el-form-item label=":" prop="classType">
          <el-checkbox-group v-model="userForm.classType" :min="0" :max="1" :disabled="userForm.accountType.length > 0 || userForm.dataType.length > 0">
            <el-checkbox
            v-for="item in ClassTypes"
            :key="item.label"
            :label="item.value">{{item.label}}</el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item label=":" prop="accountType">
          <el-checkbox-group v-model="userForm.accountType" :min="0" :max="1" :disabled="userForm.classType.length > 0 || userForm.dataType.length > 0">
            <el-checkbox
            v-for="item in AccountTypes"
            :key="item.label"
            :label="item.value">{{item.label}}</el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item label=":" prop="dataType">
          <el-checkbox-group v-model="userForm.dataType" :min="0" :max="1" :disabled="userForm.classType.length > 0 || userForm.accountType.length > 0">
            <el-checkbox
            v-for="item in DataTypes"
            :key="item.label"
            :label="item.value">{{item.label}}</el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item label=":" prop="mobile">
          <el-input v-model="userForm.mobile" style="width: auto" placeholder=""></el-input>
        </el-form-item >
        <el-row style="margin-top: 30px">
          <el-col :span="7">
            <el-form-item label="itemId:" prop="itemId">
              <el-input v-model="userForm.itemId" style="width: auto" placeholder="itemid"></el-input>
              <el-tooltip class="item" effect="dark" content="" placement="top">
                <i class="el-icon-question" style="margin-left: 5px"></i>
              </el-tooltip>
            </el-form-item>
          </el-col>
          <el-col :span="7">
            <el-form-item label=":" prop="level">
              <el-select v-model="userForm.level">
                <el-option
                  v-for="item in options"
                  :key="item.label"
                  :label="item.label"
                  :value="item.value"></el-option>
              </el-select>
              <el-tooltip class="item" effect="dark" content="" placement="top">
                <i class="el-icon-question" style="margin-left: 5px"></i>
              </el-tooltip>
            </el-form-item>
          </el-col>
          <el-col :span="7">
            <div>
              <el-form-item label=":" prop="bd">
                <el-date-picker
                  v-model="userForm.bd"
                  type="date"
                  value-format="timestamp"
                  placeholder="">
                </el-date-picker>
                <el-tooltip class="item" effect="dark" content="" placement="top">
                  <i class="el-icon-question" style="margin-left: 5px"></i>
                </el-tooltip>
              </el-form-item>
            </div>
          </el-col>
        </el-row>
        <el-form-item style="margin-top: 30px">
          <el-button type="primary" @click="userSubmit"></el-button>
          <el-button @click="resetForm('userForm')"></el-button>
        </el-form-item>
      </el-form>
      <el-dialog :visible.sync="createUser" center title="" width="30%">
        <span>mock</span>
        <span slot="footer">
          <el-button @click="createUser = false"> </el-button>
          <el-button type="primary" @click="onSubmit"> </el-button>
        </span>
      </el-dialog>
    </div>
    <div style="margin-top: 50px; margin-left: 28px">
      <span style="font-size: 14px; float: left; color: #606266"></span>
      <el-input type="textarea" :rows="10" style="margin-top: 10px" v-model="result"></el-input>
    </div>
  </el-card>



</template>

<script>
import {ClassType, AccountType, DataType} from '@/constant/dataTool'
import {dataToolApi} from "@/api";

const ClassTypes = Object.entries(ClassType).map(([value, label]) => {
  return {label: label, value: value}
})

const AccountTypes = Object.entries(AccountType).map(([value, label]) => {
  return {label: label, value: value}
})

const DataTypes = Object.entries(DataType).map(([value, label]) => {
  return {label: label, value: value}
})
export default {
  name: "generateAccount",
  data() {
    return {
      createUser: false,
      ClassTypes: ClassTypes,
      AccountTypes: AccountTypes,
      DataTypes: DataTypes,
      result: '',
      userForm: {
        classType: [],
        accountType: [],
        dataType: [],
        mobile: '',
        bd: '',
        itemId: '',
        level: '',
      },
      options: [{
        label: 'T1GE',
        value: 'T1GE',
      },
        {
          label: 'K1GE',
          value: 'K1GE',
        },
        {
          label: 'K3GE',
          value: 'K3GE',
        }]
    }
  },
  methods: {
    onSubmit() {
      this.result = ''
      console.log(this.userForm)
      // eslint-disable-next-line no-undef
      let user_params = _.cloneDeep(this.userForm);
      dataToolApi.createAccountTypeUser(user_params).then(res => {
        this.result = res.data
      })
      this.createUser = false
    },
    userSubmit() {
      this.createUser = true
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
  }
}
</script>

