<template>
  <el-card>
    <div>
      <el-form ref="lessonForm" :rules="lessonRules" :model="lessonForm" label-width="100px" style="width: 100%; margin: 20px auto">
        <el-form-item label="ID:" prop="lessonId">
          <el-input v-model="lessonForm.lessonId" style="width: 50%" placeholder="lessonId"></el-input>
        </el-form-item>
        <el-form-item label="ID:" prop="bid">
          <el-input v-model="lessonForm.bid" style="width: 50%" placeholder="bid"></el-input>
        </el-form-item>
        <el-form-item label=":" prop="isFirst">
          <el-select v-model="lessonForm.isFirst">
            <el-option
            v-for="item in options"
            :key="item.label"
            :label="item.label"
            :value="item.value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=":" prop="finishTime">
          <el-date-picker
            v-model="lessonForm.finishTime"
            type="datetime"
            value-format="timestamp"
            placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item style="margin-top: 30px">
          <el-button type="primary" @click="lessonSubmit('lessonForm')"></el-button>
          <el-button @click="resetForm('lessonForm')"></el-button>
        </el-form-item>
      </el-form>
    </div>
    <div style="margin-top: 50px; margin-left: 28px">
      <span style="font-size: 14px; float: left; color: #606266"></span>
      <el-input type="textarea" :rows="15" style="margin-top: 10px" v-model="result"></el-input>
    </div>
  </el-card>

</template>

<script>
import {dataToolApi} from "@/api"

export default {
  name: "lessonProgress",
  data() {
    return {
      lessonForm: {
        lessonId: '',
        bid: '',
        isFirst: '',
        finishTime: ''
      },
      lessonRules: {
        lessonId: [{required: true, message: "Id", trigger: "blur"}],
        bid: [{required: true, message: "id", trigger: "blur"}],
        isFirst: [{required: true, message: "", trigger: "blur"}],
        finishTime: [{required: true, message: "", trigger: "blur"}],
      },
      result: '',
      options: [
        {label: '',
        value: 1},
        {label: '',
          value: 0}
      ]
    }
  },
  methods: {
    lessonSubmit(formName) {
      this.$refs[formName].validate((valid) => {
        if(valid) {
          console.log(this.lessonForm)
          // eslint-disable-next-line no-undef
          let lesson_params = _.cloneDeep(this.lessonForm)
          dataToolApi.lessonProgress(lesson_params).then(res => {
            this.result = res.data
          })
        }
        else {
          return false
        }
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
    }
  }
}
</script>

