<template>
  <el-card>
    <div>
      <el-form ref="orderForm" status-icon :rules="orderRules" :model="orderForm" label-width="100px" style="width: 100%; margin: 20px auto">
        <el-form-item label=":" prop="subject">
          <el-radio-group v-model="orderForm.subject">
            <el-radio
            v-for="item in SubjectTypes"
            :key="item.label"
            :label="item.value">{{item.label}}</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label=":" prop="source">
          <el-input v-model="orderForm.source" style="width: auto" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":" prop="mobile">
          <el-input v-model="orderForm.mobile" style="width: auto" placeholder=""></el-input>
        </el-form-item>
        <el-form-item style="margin-top: 30px">
          <el-button type="primary" @click="onSubmit('orderForm')"></el-button>
          <el-button @click="resetForm('orderForm')"></el-button>
        </el-form-item>
      </el-form>
      <el-dialog :visible.sync="createOrder" center title="" width="30%">
        <span>fatmock</span>
        <span slot="footer">
          <el-button @click="createOrder = false"> </el-button>
          <el-button type="primary" @click="orderSubmit"> </el-button>
        </span>
      </el-dialog>
    </div>
        <div style="margin-top: 50px; margin-left: 28px">
          <span style="font-size: 14px; float: left; color: #606266"></span>
          <el-input type="textarea" :rows="10" style="margin-top: 10px"></el-input>
        </div>
  </el-card>

</template>

<script>
import {dataToolApi} from "@/api";
import {SubjectType} from "@/constant/dataTool"

const SubjectTypes = Object.entries(SubjectType).map(([value, label]) => {
  return {label: label, value: value}
})

export default {
  name: "sampleOrder",
  data() {
    return {
      createOrder: false,
      SubjectTypes: SubjectTypes,
      orderForm: {
        subject: [],
        source: '',
        mobile: '',
      },
      orderRules: {
        subject: [{required: true, message: '', trigger: 'blur'}],
        source: [{required: true, message: '', trigger: 'blur'}],
        mobile: [{required: true, message: '', trigger: 'blur'}],
      },
      result: '',
    }

  },
  methods: {
    onSubmit(formName) {
      this.$refs[formName].validate((valid) => {
        if(valid){
          this.createOrder = true;
        }
        else {
          return false
        }
      });
    },
    orderSubmit() {
      console.log(this.orderForm)
      // eslint-disable-next-line no-undef
      let order_params = _.cloneDeep(this.orderForm);
      dataToolApi.createSampleOrder(order_params).then(res => {
        this.result = res.data
      })
      this.createOrder = false
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
  }
}
</script>

