<template>

  <common-component title="">

    <template>

      <my-pie-chart
        v-if="isShow"
        text=" "
        name=""
        :data="chartData"
        :radius="radius"
        height="400px"
        width="500px"/>

    </template>

  </common-component>

</template>

<script>
import myPieChart from "@/common/components/myPieChart";
import CommonComponent from "./CommonComponent";

export default {
  name: "TestResultChartComponent",
  components: {myPieChart, CommonComponent},
  data() {
    return {
      dataMap: new Map([
        ["pass", {name: '', itemStyle: {color: '#67C23A'}}],
        ["fail", {name: '', itemStyle: {color: '#F56C6C'}}],
        ["block", {name: '', itemStyle: {color: '#E6A23C'}}],
        ["skip", {name: '', itemStyle: {color: '#909399'}}],
        ["init", {name: '', itemStyle: {color: '#DEDE10'}}]
      ]),
      isShow: true,
      radius: ['40%', '70%']
    }
  },
  props: {
    data: {
      type: Object,
    }
  },
  computed: {
    chartData() {
      return this.getChartData()
    }
  },
  // created() {
  //   this.getChartData();
  // },
  methods: {
    getChartData() {
      var chartData = []
      Object.keys(this.data).forEach(item => {
        let data = this.dataMap.get(item);
        data.value = this.data[item]
        chartData.push(data)
      });
      this.reload();
      return chartData


    },
    reload() {
      this.isShow = false;
      this.$nextTick(function () {
        this.isShow = true;
      })
    }
  }
}
</script>

