exports.install = function (Vue) {
    Vue.prototype.sureView = function (func, arg, msg) {
        // this.$alert('?', '', {
        //                 confirmButtonText: '',
        //                 callback: action => {
        //                     func(arg);
        //                 }
        //             });
        this.$confirm('' + '<strong style="color: red;">' + msg + '</strong>' + ',?', '', {
            dangerouslyUseHTMLString: true,
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
        }).then(() => {
            func(arg);
        })
    };
    Vue.prototype.messageShow = function (_this, response) {
        if (response.data['status'] === 0) {
            _this.$message({
                showClose: true,
                message: response.data['msg'],
                type: 'warning',
            });
            // if(response.data['error']){
            //     return 'error'
            // }
            return false
        } else {
            if (response.data['msg']) {
                _this.$message({
                    showClose: true,
                    message: response.data['msg'],
                    type: 'success',
                });
            }

            return true
        }
    };
};

