<template>
  <div>
    <el-card style="margin-top: 20px">
      <div>
        <el-form ref="updateForm" :model="updateForm" label-width="100px" style="width: 100%; margin: 20px auto">
          <el-form-item label=":" prop="mobile">
            <el-input v-model="updateForm.mobile" style="width: 200px" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label=":" prop="state">
            <el-select v-model="updateForm.state" style="width: 200px">
              <el-option
              v-for="item in options"
              :key="item.label"
              :label="item.label"
              :value="item.value"></el-option>
            </el-select>
            <el-tooltip class="item" effect="dark" content="" placement="top">
              <i class="el-icon-question" style="margin-left: 5px"></i>
            </el-tooltip>
          </el-form-item>
          <el-form-item style="margin-top: 30px">
            <el-button type="primary" @click="queryClass"></el-button>
            <el-button type="primary" @click="deleteAllClass"></el-button>
            <el-button type="primary" @click="deleteUser"></el-button>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="deleteUserSample"></el-button>
            <el-button type="primary" @click="updatepromoter"></el-button>
            <el-button @click="resetForm('updateForm')"></el-button>
          </el-form-item>
        </el-form>
      </div>
      <div style="margin-top: 50px; margin-left: 28px">
        <span style="font-size: 14px; float: left; color: #606266"></span>
        <el-input type="textarea" :rows="15" style="margin-top: 10px" v-model="result"></el-input>
      </div>
      <div>
        <el-dialog :visible.sync="deleteClass" center title="" width="30%">
          <span></span>
          <span slot="footer">
          <el-button @click="deleteClass = false"> </el-button>
          <el-button type="primary" @click="deleteSubmit"> </el-button>
        </span>
        </el-dialog>
        <el-dialog :visible.sync="cancelUser" center title="" width="30%">
          <span></span>
          <span slot="footer">
          <el-button @click="cancelUser = false"> </el-button>
          <el-button type="primary" @click="deleteUserSubmit"> </el-button>
        </span>
        </el-dialog>
        <el-dialog :visible.sync="deleteSample" center title="" width="30%">
          <span></span>
          <span slot="footer">
          <el-button @click="deleteSample = false"> </el-button>
          <el-button type="primary" @click="deleteSampleSubmit"> </el-button>
        </span>
        </el-dialog>
      </div>
    </el-card>
  </div>

</template>

<script>
import {dataToolApi} from "@/api";

export default {
  name: "updateUser",
  data() {
    return {
      updateForm: {
        mobile: '',
        state: '',
        date: '',
      },
      options: [{
        label: '',
        value: 'active',
      },
        {
          label: '',
          value: 'frozen',
        },
        {
          label: '',
          value: 'forbidden',
        },{
          label: '',
          value: 'inactive',
        },
        {
          label: '',
          value: 'invalid',
        }],
      result: '',
      deleteClass: false,
      cancelUser: false,
      deleteSample: false
    }
  },
  methods: {
    queryClass() {
      console.log(this.updateForm)
      // eslint-disable-next-line no-undef
      let query_params = _.cloneDeep(this.updateForm);
      dataToolApi.getUserClass(query_params).then(res => {
        console.log(res.data)
        this.result = res.data
      })
    },
    deleteAllClass() {
      this.deleteClass = true
    },
    deleteUser() {
      this.cancelUser = true
    },
    deleteUserSample() {
      this.deleteSample = true
    },
    deleteSubmit() {
      // eslint-disable-next-line no-undef
      let delete_params = _.cloneDeep(this.updateForm);
      dataToolApi.deleteUserAllClass(delete_params).then()
    },
    deleteUserSubmit() {
      // eslint-disable-next-line no-undef
      let delete_user_params = _.cloneDeep(this.updateForm)
      dataToolApi.deleteAccount(delete_user_params).then()
      this.cancelUser = false
    },
    deleteSampleSubmit() {
      // eslint-disable-next-line no-undef
      let delete_sample_params = _.cloneDeep(this.updateForm)
      dataToolApi.deleteUserSampleClass(delete_sample_params).then()
      this.deleteSample = false
    },
    updatepromoter() {
      // eslint-disable-next-line no-undef
      let promoter_params = _.cloneDeep(this.updateForm);
      dataToolApi.updatePromoterState(promoter_params).then()
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
  }
}
</script>

<style scoped lang="scss">
.title_top{
  margin-top: 20px;
  color: #909399;
  font-size: 16px;
  font-weight: 600;
  float: left;
}
</style>
