<template>
  <common-component title="sonar">
    <template>
      <el-table size="large"
                :data="tableData"
                :cell-style="cellStyle">
        <el-table-column prop="appName" label=""/>
        <el-table-column prop="lang" label=""/>
        <el-table-column prop="checkResult" label="">
          <template v-slot:default="scope">
            <el-tag :type="tagType(scope.row.checkResult)" effect="dark">{{scope.row.checkResult}}</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="blocker" label="Blocker"/>
        <el-table-column prop="critical" label="Critical"/>
        <el-table-column prop="sonarUrl" label="">
          <template slot-scope="scope">
            <a :href="scope.row.sonarUrl" target="_blank">
              {{scope.row.appName}}</a>
          </template>
        </el-table-column>
      </el-table>
    </template>

  </common-component>


</template>

<script>
import {testPlanApi} from "@/api"
import CommonComponent from "@/pages/track/plan/view/comonents/report/components/CommonComponent"

export default {
  name: "SonarResult",
  components: {CommonComponent},
  props: {
    pipelineIds: {
      type: Array,
      default() {
        return []
      }
    }
  },
  data() {
    return {
      tableData: []
    }
  },
  // watch: {
  //   pipelineIds() {
  //     console.log('pipelineIdsidsids', this.pipelineIds)
  //     this.getTableData()
  //   }
  // },
  mounted() {
    this.getTableData()
  },
  methods: {
    tagType(status){
      if (status === '') {
        return 'success'
      }else if(status === '') {
        return 'danger'
      }
    },
    cellStyle(){
      return 'font: 13px Arial;font-weight: 500; color: #606266;'
    },
    getTableData() {
      console.log('pipelineids', this.pipelineIds)
      if (this.pipelineIds.length > 0)
      {
        testPlanApi.getSonarResult({pipelineIds: this.pipelineIds}).then(res => {
          this.tableData = res.data.data
        })
      }

    }
  }
}
</script>

