<template>
  <div>
    <el-row>
      <el-col :span="8" style="padding: 10px">
        <el-card style="height: 260px">
          <span style="color: #909399; font-size: 18px;"></span>
          <div>
            <el-form ref="refundForm" :model="refundForm" style="width: 100%;" label-width="80px">
              <el-form-item label=":" prop="mobile">
                <el-input v-model="refundForm.mobile" style="width: 85%;" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label=":" prop="orderNo">
                <el-input v-model="refundForm.orderNo" style="width: 85%;" placeholder="Id"></el-input>
              </el-form-item>
            </el-form>
            <div style="margin-top: 30px">
              <el-button type="primary" @click="refundSubmit"></el-button>
              <el-button @click="resetForm('refundForm')"></el-button>
            </div>
          </div>
        </el-card>
      </el-col>
      <el-col :span="8" style="padding: 10px">
        <el-card style="height: 260px">
          <span style="color: #909399; font-size: 18px;"></span>
          <div>
            <el-form ref="iosForm" :model="iosForm"  style="width: 100%;" label-width="80px">
              <el-form-item label="IOS:" prop="version">
                <el-input v-model="iosForm.version" style="width: 85%;" placeholder="IOS"></el-input>
              </el-form-item>
            </el-form>
            <div style="margin-top: 40px">
              <el-button type="primary" @click="updateGlobal"></el-button>
              <el-button @click="resetForm('iosForm')"></el-button>
            </div>
          </div>
        </el-card>
      </el-col>
      <el-col :span="8" style="padding: 10px">
        <el-card style="height: 260px">
          <span style="color: #909399; font-size: 18px;"></span>
          <div>
            <el-form ref="clockForm" :rules="clockRules" :model="clockForm" style="width: 100%;" label-width="90px">
              <el-form-item label="guaid/uid:" prop="id">
                <el-input v-model="clockForm.id" style="width: 85%;" placeholder="guaiduid"></el-input>
              </el-form-item>
              <el-form-item label=":" prop="days">
                <el-input-number v-model="clockForm.days" :min="0" style="width: 85%" placeholder=""></el-input-number>
              </el-form-item>
              <el-form-item label="Id:" prop="task">
                <el-select v-model="clockForm.task">
                  <el-option
                    v-for="item in options"
                    :key="item.label"
                    :label="item.label"
                    :value="item.label"></el-option>
                </el-select>
              </el-form-item>
            </el-form>
            <div>
              <el-button type="primary" @click="punchCard('clockForm')"></el-button>
              <el-button @click="resetForm('clockForm')"></el-button>
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="8" style="padding: 10px">
        <el-card style="height: 260px">
          <span style="color: #909399; font-size: 18px;"></span>
          <div>
            <el-form ref="refundForm" :model="refundForm" style="width: 100%;" label-width="80px">
              <el-form-item label=":" prop="email">
                <el-input v-model="refundForm.email" style="width: 85%;" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label=":" prop="nick">
                <el-input v-model="refundForm.nick" style="width: 85%;" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label=":" prop="password">
                <el-input v-model="refundForm.password" style="width: 85%;" placeholder=""></el-input>
              </el-form-item>
            </el-form>
            <div style="margin-top: 30px">
              <el-button type="primary" @click="refundCreate"></el-button>
              <el-button @click="resetForm('refundForm')"></el-button>
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>

</template>

<script>
import {dataToolApi} from "@/api";

export default {
  name: "otherScene",
  data() {
    return {
      refundForm: {
        mobile: '',
        orderNo: '',
        email: '',
        nick: '',
        password: '123456',

      },
      iosForm: {
        version: '',
        status: '',
      },
      clockForm: {
        id: '',
        task: '',
        days: 0,
      },
      options: [{
        label: 'i1',
      },
        {
          label: 'i2',
        },
        {
          label: 'i3',
        },
        {
          label: 'i4',
        },
        {
          label: 'i5',
        },
        {
          label: 'i6',
        }],
      clockRules: {
        id: [{required: true, message: "guaiduid", trigger: "blur"}],
        task: [{required: true, message: "Id", trigger: "blur"}],
        days: [{required: true, message: "", trigger: "blur"}],
      },
      result: '',
    }
  },
  methods: {
    punchCard(formName) {
      this.$refs[formName].validate((valid) => {
        if(valid) {
          // eslint-disable-next-line no-undef
          let clock_params = _.cloneDeep(this.clockForm);
          dataToolApi.resetUserCheckinDays(clock_params).then()
        }
        else {
          return false
        }
      });
    },
    refundCreate() {
      this.$alert('', '', {
        confirmButtonText: '',
        showCancelButton: true,
        cancelButtonText: '',
        callback: (action) => {
          if (action === 'confirm') {
            // eslint-disable-next-line no-undef
            let refund_params = _.cloneDeep(this.refundForm);
            dataToolApi.orderRefund(refund_params).then()
          }
        }
      })
    },
    updateGlobal() {
      this.$alert('', '', {
        confirmButtonText: '',
        showCancelButton: true,
        cancelButtonText: '',
        callback: (action) => {
          if (action === 'confirm') {
            // eslint-disable-next-line no-undef
            let global_params = _.cloneDeep(this.iosForm);
            dataToolApi.updateGlobal(global_params).then()
          }
        }
      })
    },
    refundSubmit() {
      this.$alert('', '', {
        confirmButtonText: '',
        showCancelButton: true,
        cancelButtonText: '',
        callback: (action) => {
          if (action === 'confirm') {
            // eslint-disable-next-line no-undef
            let refund_params = _.cloneDeep(this.refundForm);
            dataToolApi.orderRefund(refund_params).then()
          }
        }
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
  }
}
</script>

