<template>
  <div>
    <el-card style="margin-top: 20px">
      <div slot="header" class="title">
        <span></span>
        <el-date-picker
          v-model="month"
          :clearable="false"
          placeholder=""
          type="month"
          value-format="yyyy-MM"
          style="width: 200px;float: right; padding: 3px 0; border: 0;"
        />
      </div>
      <el-table :data="tableData"
                row-key="id"
                size="medium"
                v-loading="loading" style="margin-top: -10px">
        <template v-for="col in columns">
          <el-table-column :prop="col.prop" :label="col.label" :key="col.prop" :width="col.width">
            <template slot-scope="scope">
              <el-input v-if="col.editable" style="width: 60%"
                        @change="changeMark(scope.row)"
                        @keyup.enter.native="$event.target.blur"
                        v-model="scope.row[col.prop]"></el-input>
              <span v-else>{{scope.row[col.prop]}}</span>
            </template>
          </el-table-column>
        </template>
      </el-table>
    </el-card>
  </div>

</template>

<script>
import {jiraApi} from "@/api";

export default {
  name: "MonthResourceReport",
  data() {
    return{
      loading: false,
      month: '',
      tableData: [],
      columns: [
        {
          label: "",
          prop: "departName",
          editable: false,
        },
        {
          label: "",
          prop: "leader",
          editable: false,
        },
        {
          label: "",
          prop: "peopleCount",
          editable: false,
        },
        {
          label: "",
          prop: "workDays",
          editable: false,
        },
        {
          label: "",
          prop: "totalWorkingDays",
          editable: false,
        },
        {
          label: ",",
          prop: "preDepartmentDays",
          editable: false,
        },
        {
          label: ",",
          prop: "currentDepartmentDays",
          editable: false,
        },
        {
          label: ",",
          prop: "nextDepartmentDays",
          editable: false,
        },
        {
          label: "",
          prop: "targetDaysDiff",
          editable: false,
        },
        {
          label: "",
          prop: "productivity",
          editable: false,
        },
        {
          label: "",
          prop: "mark",
          editable: true,
        }
      ]
    }

  },
  mounted() {
    this.getdatatime()
  },
  watch: {
    month() {
      this.getMonthReport()
    }
  },
  methods: {
    getdatatime(){
      this.month = new Date().format("yyyy-MM");
    },
    getMonthReport(){
      this.loading = true
      jiraApi.getMonthResourceReport({month: this.month}).then(res => {
        this.tableData = res.data.map(item => {
          return{
            id: item.id,
            departName: item.departName,
            leader: item.leader,
            peopleCount: item.peopleCount,
            workDays: item.workDays,
            totalWorkingDays: item.totalWorkingDays,
            preDepartmentDays: item.preDepartmentDays,
            currentDepartmentDays: item.currentDepartmentDays,
            nextDepartmentDays: item.nextDepartmentDays,
            targetDaysDiff: item.targetDaysDiff,
            productivity: item.productivity,
            mark: item.mark,
            editable: item.editable
          }
        })
        this.loading = false
      })

    },
    changeMark(row) {
      jiraApi.editMonthResourceReport(row.id, row).then(
        // eslint-disable-next-line no-unused-vars
        res => {
          this.getMonthReport()
        }
      )
    },
  }
}
</script>

<style scoped lang="scss">
.title {
  font-size: 20px;
  font-weight: 700;
  margin-left: 5px;
  line-height: 35px;
  text-align: left;
}
</style>
