<template>
  <el-card>
    <div>
      <el-form ref="shipForm" status-icon :rules="shipRules" :model="shipForm" label-width="100px" style="width: 100%; margin: 20px auto">
        <el-form-item label=":" prop="channel">
          <el-radio-group v-model="shipForm.channel">
            <el-radio
            v-for="item in ChannelTypes"
            :key="item.label"
            :label="item.value">{{item.label}}</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label=":" prop="subject">
          <el-radio-group v-model="shipForm.subject">
            <el-radio
            v-for="item in SubTypes"
            :key="item.label"
            :label="item.value">{{item.label}}</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label=":" prop="fanType">
          <el-radio-group v-model="shipForm.fanType">
            <el-radio
            v-for="item in Fantypes"
            :key="item.label"
            :label="item.value">{{item.label}}</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label=":" prop="mobile">
          <el-input v-model="shipForm.mobile" style="width: auto" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":" prop="fanMobile">
          <el-input v-model="shipForm.fanMobile" style="width: auto" placeholder=""></el-input>
        </el-form-item>
        <el-form-item style="margin-top: 30px">
          <el-button type="primary" @click="onSubmit('shipForm')"></el-button>
          <el-button @click="resetForm('shipForm')"></el-button>
        </el-form-item>
      </el-form>
      <el-dialog :visible.sync="createRelation" center title="" width="30%">
        <span></span>
        <span slot="footer">
          <el-button @click="createRelation = false"> </el-button>
          <el-button type="primary" @click="shipSubmit"> </el-button>
        </span>
      </el-dialog>
    </div>
  </el-card>

</template>

<script>
import {dataToolApi} from "@/api";
import {ChannelType, SubType, FanType} from '@/constant/dataTool'

const ChannelTypes = Object.entries(ChannelType).map(([value, label]) => {
  return {label: label, value: value}
})

const SubTypes = Object.entries(SubType).map(([value, label]) => {
  return {label: label, value: value}
})

const FanTypes = Object.entries(FanType).map(([value, label]) => {
  return {label: label, value: value}
})

export default {
  name: "relationship",
  data() {
    return {
      createRelation: false,
      ChannelTypes: ChannelTypes,
      SubTypes: SubTypes,
      Fantypes: FanTypes,
      shipForm: {
        channel: '',
        subject: '',
        fanType: '',
        mobile: '',
        fanMobile: '',
      },
      shipRules: {
        channel: [{required: true, message: '', trigger: 'blur'}],
        subject: [{required: true, message: '', trigger: 'blur'}],
        fanType: [{required: true, message: '', trigger: 'blur'}],
        mobile: [{required: true, message: '', trigger: 'blur'}],
        fanMobile: [{required: true, message: '', trigger: 'blur'}],
      },
    }

  },
  methods: {
    onSubmit(formName) {
      this.$refs[formName].validate((valid) => {
        if(valid){
          this.createRelation = true;
        }
        else {
          return false
        }
      });
    },
    shipSubmit() {
      console.log(this.shipForm)
      // eslint-disable-next-line no-undef
      let ship_params = _.cloneDeep(this.shipForm);
      dataToolApi.xshareRelationshipData(ship_params).then()
      this.createRelation = false
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
  }
}
</script>

