<template>
  <el-card>
    <div>
      <i class="q-icon fa fa-user"></i>
      <el-breadcrumb separator="/">
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-form ref="addForm" :model="editData" :rules="rules" class="addForm" label-width="100px">
      <el-form-item label=" ">
        <p style="color:#999;"> </p>
      </el-form-item>
      <el-form-item label="" prop="oldpassword">
        <el-input v-model="editData.oldpassword" type="password"></el-input>
      </el-form-item>
      <el-form-item label="" prop="newpassword">
        <el-input v-model="editData.newpassword" type="password"></el-input>
      </el-form-item>
      <el-form-item label="" prop="newpassword2">
        <el-input v-model="editData.newpassword2" type="password"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button @click="cancelFun"></el-button>
        <el-button type="primary" @click="sureFun"></el-button>
      </el-form-item>
    </el-form>
  </el-card>
</template>
<script>
import userApi from "@/api/user"

export default {
  data: function () {
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else if (value !== this.editData.newpassword) {
        callback(new Error('!'));
      } else {
        callback();
      }
    };
    return {
      editData: {
        oldpassword: "",
        newpassword: "",
        newpassword2: "",
      },
      rules: {
        oldpassword: [
          {required: true, message: "", trigger: "blur"}
        ],
        newpassword: [
          {required: true, message: "", trigger: "blur"}
        ],
        newpassword2: [
          {required: true, validator: validatePass2, trigger: "blur"}
        ]
      }
    };
  },
  computed: {
    userId() {
      return this.$store.state.login.userid;
    },
    userName() {
      return this.$store.state.login.username;
    }
  },
  created() {
    // 
    this.$store.commit('SET_ASIDEHIDDEN', true)
  },
  methods: {
    cancelFun() {
      this.$store.commit('SET_ASIDEHIDDEN', false)
      this.$router.go(-1); //
    },
    sureFun() {
      this.$refs.addForm.validate(valid => {
        if (valid) {
          let params = {
            oldpassword: this.editData.oldpassword,
            newpassword: this.editData.newpassword
          };
          userApi.resetPassword(this.userId, params).then(
              // eslint-disable-next-line no-unused-vars
              res => {
                this.$message.success({
                  message: "",
                  type: "success"
                });
                this.$store.commit('SET_ASIDEHIDDEN', false)
                this.$router.go(-1); //
              },
          );
        }
      });
    },
  },
  mounted() {
  }
};
</script>
<style lang="scss" scoped>
.addForm {
  width: 500px;
}
</style>
